import React from "react";
import { useEffect } from "react";
import { useRef } from "react";
import banner from "../imgs/banner.jpg";
import "./jumbotron.css";

export default function Jumbotron() {
  const header = useRef(null);
  const image = useRef(null);
  useEffect(() => {
    if (!header.current || !image.current) {
      return;
    }
    const eventHandler = header.current.addEventListener("mousemove", (e) => {
      let percentage = e.clientX / window.outerWidth; // 计算游标X（水平方向）位置除以视窗宽度，得出百份比
      let ratio = 20 * percentage; // 位移的初始值
      ratio *= 1.3;
      image.current.style.setProperty("--offset", `${ratio}px`);
    });
    const temp = header.current;
    return () => temp.current.removeEventListener("mousemove", eventHandler);
  }, [header, image]);

  return (
    <header className="header" ref={header}>
      <div>
        <img src={banner} ref={image} />
      </div>
    </header>
  );
}
