const files = require.context("./mkdowns", false);
export const titles = files.keys();
// Let's get even more succint...
const importAll = (r) => r.keys().map(r);
const markdownFiles = importAll(require.context("./mkdowns", false, /\.md$/));

export const promises = markdownFiles.map((file) =>
  fetch(file).then((res) => res.text())
);
